import { Component, Inject, OnInit } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { fuseAnimations } from '@fuse/animations';
import { JedsMenuService } from '@shared/service/app/menu.service';
import { IProveedor } from 'app/modules/administracion/proveedores/models/proveedor';
import { PedidosDePresupuestoService } from 'app/modules/compras/pedidos-de-presupuesto/service/pedidos-de-presupuesto.service';

@Component({
  selector: 'app-filter-menu-pedidos-de-presupuesto',
  templateUrl: './filter-menu.component.html',
  styleUrls: ['./filter-menu.component.scss'],
  animations: fuseAnimations
})
export class FilterMenuComponent implements OnInit {
  public formFilters: FormGroup;
  public proveedores: IProveedor[] = [];
  constructor(@Inject('MENU_DATA') public data: any, private menuService: JedsMenuService, private _pedidoDePresupuestoService: PedidosDePresupuestoService) { }

  ngOnInit(): void {
    
    this.formFilters = new FormGroup({
      proveedor: new FormControl((''))
    });
    this.formFilters.patchValue(this.data.filter);
    this.getProveedores();
  }

  resetFilters() {
    this.formFilters.reset({
      proveedor: -1,
    });
  }

  onCloseMenu(data: any = null) {
    this.menuService.close(data);
  }

    /**
    * Esta funcion es para listas los proveedores en el filtro de grilla.
    * @returns 
    */
    getProveedores() {
      const proveedores = this._pedidoDePresupuestoService.getProveedores().subscribe(proveedor => { this.proveedores = proveedor;})
      console.log(proveedores)
      return proveedores;
    }
}